/*** PRELOADER ***/
.preload {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: 99999;
  background: #b80000;
  transition: opacity 0.5s ease;
}

.loader {
  -webkit-animation: spinload 1s linear 0s infinite forwards;
          animation: spinload 1s linear 0s infinite forwards;
  width: 100px;
}

@-webkit-keyframes spinload {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes spinload {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.preload-finish {
  opacity: 0;
  pointer-events: none;
}

