  position: fixed;
  top: 80px;
  right: -60px;
  z-index: 10;
  -webkit-animation: popin 0.5s linear 5s 1 forwards;
          animation: popin 0.5s linear 5s 1 forwards;
}

.sidebarMenu {
  display: flex;
  flex-direction: column;
  font-size: 20px;
