/*** THROWING STARS ***/
.fly {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 20%;
  left: 90%;
  opacity: 0;
  z-index: 999;
  -webkit-animation: slidein 0.2s linear 8s 1 forwards;
          animation: slidein 0.2s linear 8s 1 forwards;
}

.fly2 {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 80%;
  right: 90%;
  opacity: 0;
  z-index: 999;
  -webkit-animation: slidein2 0.2s linear 10s 1 forwards;
          animation: slidein2 0.2s linear 10s 1 forwards;
}

.rotate {
  animation: rotation 0.2s linear 8s 1 reverse;
}

.rotate2 {
  -webkit-animation: rotation 0.2s linear 10s 1 forwards;
          animation: rotation 0.2s linear 10s 1 forwards;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(3000deg);
            transform: rotate(3000deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(3000deg);
            transform: rotate(3000deg);
  }
}

@-webkit-keyframes slidein {
  0% {
    left: 90%;
    top: 20%;
    opacity: 0;
    width: 40px;
  }
  1% {
    left: 90%;
    top: 20%;
    opacity: 1;
    width: 40px;
  }
  100% {
    left: -30px;
    top: 40%;
    opacity: 1;
    width: 120px;
  }
}

@keyframes slidein {
  0% {
    left: 90%;
    top: 20%;
    opacity: 0;
    width: 40px;
  }
  1% {
    left: 90%;
    top: 20%;
    opacity: 1;
    width: 40px;
  }
  100% {
    left: -30px;
    top: 40%;
    opacity: 1;
    width: 120px;
  }
}

@-webkit-keyframes slidein2 {
  0% {
