  }
}

/*** NINJA FADEIN ***/
.ninja-fade {
  -webkit-filter: blur(100px);
          filter: blur(100px);
  -webkit-animation: blurin 2s linear 3s forwards;
          animation: blurin 2s linear 3s forwards;
}

@-webkit-keyframes blurin {
  0% {
    -webkit-filter: blur(100px);
  }
  100% {
    -webkit-filter: blur(0px);
  }
}

@keyframes blurin {
  0% {
    -webkit-filter: blur(100px);
  }
  100% {
    -webkit-filter: blur(0px);
  }
}

/*** HEADER POP ***/
.hero .inner2 {
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  opacity: 0;
  -webkit-animation: pop 0.2s linear 3s forwards;
          animation: pop 0.2s linear 3s forwards;
}

@-webkit-keyframes pop {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0;
  }
  80% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes pop {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    opacity: 0;
  }
  80% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

